.modal__preview {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__content {
  max-width: 498px;
  background-color: white;
  font-family: "Cabinet-Grotesk";
  font-size: 20px;
  line-height: 24px;
  font-weight: bold;
  padding: 0 0 24px 0;
  position: relative;
  border-radius: 20px;
}

.modal__close:hover {
  cursor: pointer;
}

.modal__close-preview {
  position: absolute;
  top: 23px;
  right: 23px;
  width: 16px;
  height: 16px;
  background-size: 16px;
  background-image: url(../../images//close-icon.svg);
  border: none;
}

.modal__close-preview:hover {
  cursor: pointer;
}

.modal__image {
  width: 498px;
  height: 498px;
  /* min-width: 498px;
  min-height: 498px; */
  border-top-right-radius: 16px;
  border-top-left-radius: 16px;
}

.modal__description-wrap {
  display: flex;
  flex-direction: space-between;
  justify-content: space-between;
  justify-items: auto;
}

.modal__description {
  margin: 8px 0 0 32px;
}

@media screen and (max-width: 560px) {
  .modal__container {
    max-width: 250px;
  }
  .modal__image {
    max-width: 250px;
  }
}